.about--card {
  background-color: transparent;
  border: 0;
  padding: 0.25rem;
  @include transition(0.3s);
  &:hover {
    background-color: white;
    @include border-radius(16px);
    @include transition(0.3s);
  }
  .card-body {padding: 1rem 0.75rem 0.75rem 0.75rem;}
  .about--card__img {
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      height: 30px;
      width: auto;
    }
  }
  .card-title {margin: 0;}
  .about--card__subtitle {
    font-size: 1rem;
    font-weight: 300;
    margin-bottom: 0.2rem;
  }
  .card-text {
    font-size: 1rem;
    margin-top: 0.5rem;
  }
}

.project--card {
  border: 0;
  background-color: #F6F6F6;
  @include border-radius(16px);
  height: 465px;
  width: 390px;
  position: relative;
  padding: 1rem 2rem 1rem 1rem;
  color: $text-color;
  @include media-breakpoint-down(lg) {width: 360px;}
  @include media-breakpoint-down(sm) {width: auto;}
  .JNK__link {
    position: absolute;
    bottom: 1rem;
    right: 1rem;
  }
}

.card__bg-color {
  @include border-radius(16px);
  position: relative;
  width: 95%;
  height: 445px;
  &:after {
    content: "";
    position: absolute;
    left: -3%;
    right: -3%;
    bottom: -3%;
    top: -3%;
    background-size: cover;
  }
}


.project-grid-card {
  @include media-breakpoint-down(lg) {
    height: 400px;
    width: 400px;
  }
  @include media-breakpoint-down(md) {
    height: 300px;
    width: 300px;
  }
  @include media-breakpoint-down(sm) {
    height: 350px;
    width: 350px;
  }
  height: 500px;
  width: 500px;
  margin: 1rem;
  background-size: cover;
  background-repeat: no-repeat;
}
